.loginMain {
  width: 100%;
  height: 100%;
  background: #f5f5f5 url(../../images/login_bg-05f3b32a.svg);
  overflow-y: hidden;
  background-size: 100%;
  padding: 30px;
  .loginMain_1 {
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.8);
    border-radius: 15px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    padding: 0 60px;
  }
  .loginLeft {
    width: 40%;
    img {
      width: 100%;
    }
  }
  .loginRIght {
    padding: 30px;
    border-radius: 15px;
    box-shadow: 2px 3px 7px #0003;
    width: 500px;
    .loginRIght_top {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 40px;
      p {
        font-size: 48px;
        font-weight: bold;
        color: #475768;
        margin-bottom: 0;
      }
      img {
        width: 80px;
      }
    }
    .site-form-item-icon {
      color: #666;
    }
    button {
      width: 40%;
    }
  }
}
.ant-input-affix-wrapper {
  padding: 10px 11px;
}
.loginButton {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 20px;
}
